* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

body {
    font-family: 'Poppins', sans-serif;
    overflow-x: hidden;
}

.logo img {
    height: 10vh;
    top: 255px;
    bottom: 0px;
    position: relative;
    right: 0px;
    left: 40px;
    filter: contrast(148%);
    filter: drop-shadow(5px 5px 15px #353f42);
    animation-name: appear;
    animation-duration: 2s;
    animation-timing-function: ease-in-out;
}

.logo2 img {
    height: 100%;
    position: absolute;
    bottom: 0px;
    top: 130px;
    right: 0px;
    left: 450px;
    image-rendering: crisp-edges;
    padding-bottom: 10px;
    animation-name: go-in;
    animation-duration: 4s;
    transform: translateY(50px);
   
    


}

.parall {
    position: relative;
    min-height: 38vw;
    width: 135%;
    right: 0px;
    left: 140px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background: white;
    transform: skew(20deg);
    position: relative;
    margin-left: 49em;
    top: 80px;
    
}

.frame {
    width: 90%;
    height: 75%;
    margin-left: 80px;
    top: 100px;
    right: 80px;
    border: 1px solid #D0A85D;
    position: absolute;
    align-items: center;
    justify-content: center;
    display: block;
    border-radius: 2%;
    z-index: 1;
}

.logos-container2 {
    position: relative;
}

ul {
    padding: 20px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
    justify-items: center;  

}
.champion-name {
    letter-spacing: 1px;
    display: block;
    color: black;
    padding: 1rem 0;
    font-size: 1.15rem;
    font-weight: bold;
}

.champion-link p {
    margin: 0;
}

.champion-image {
    height: 100px;
    width: 100px;
    border-radius: 50%;
    box-shadow: 0px 10px 20px -2px rgb(109, 107, 107);
    box-shadow: 0 0 0 4px #D3BD94;
    transition: box-shadow 0.6s ease-in-out;
    transition: 1s ease;     

}


.champion-image:hover {
    border: 7px solid rgb(255, 255, 255);
    box-shadow: 0 0 0 10px #D3BD94;
    box-sizing: border-box;
    animation-timing-function: ease-in-out;
    transition: all 400ms ease;
    opacity: 0.75;
}

.rectangle {
    height: 100px;
    width: 540px;
    background-color: #B8F2FF;
    margin-top: 100px;
    margin-left: auto;
    margin-right: auto;
    animation-name: appear;
    animation-duration: 4s;
    animation-timing-function: ease-in-out;
}

.rectangle > h3 {
    color: #181F20;
    font-size: 50px;
    padding-top: 15px;
    font-weight: normal;
}

.rectangle > h3 > text {
    font-weight: bold;

}


/* search bar */

.searchbar-container {
    padding-top: 64px;
}

.searchinp {
    width: 30%;
    padding: 12px 24px;
    transition: all 250ms ease-in-out;
    font-size: 18px;
    line-height: 18px;
    color: #948568;
    margin-bottom: 30px;
 
    
}

.searchinp::placeholder {
    color: rgba(87, 87, 86, 0.8);
    text-transform: uppercase;
    letter-spacing: 1.5px;

}

.searchinp:hover, .searchinp:focus {
    padding: 12px 0px 12px 12px;
    outline: 0;
    border: 1px solid transparent;
    border-bottom: 1px solid #575756;
    border-radius: 0;
    transition: all 400ms ease-in-out;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
}

